@tailwind base;
@tailwind components;
@tailwind utilities;
hero {
  display: block;
  position: relative;
  height: 100vh;
  width: 100vw;
  background-image: linear-gradient(#2B6869, #D2CFCE);
}

.hero-background {
  z-index: 1;
}

.hero-logo {
  z-index: 2;
  position: absolute;
  top: 0;
}

.hero-foreground {
  position: absolute;
  width: 100%;
  top: 0;
  z-index: 4;
}

.hero-squabs {
  z-index: 5;
  position: absolute;
  bottom: -186px;
  width: 900px;
  left: 50%;
  transform: translateX(-50%);
}